import {
	mapState,
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			textCode:"获取验证码",
			phoneNumber:'',
			captchaCode:'',
			imgHeight:'',
			loginType:'',
		}
	},
	onLoad() {
		this.load();
	},
	methods: {
		...mapMutations(['login']),
		async load(){
			var that=this;
			uni.getSystemInfo({
			　　success: function(res) { // res - 各种参数
					that.imgHeight=res.windowHeight;
			       }
			});
		},
		judgeNum(val){  //只能输入数字
			var regPos = /^\d+(\.\d+)?$/; //非负浮点数
			    var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
			    if(regPos.test(val) || regNeg.test(val)) {
			        return true;
			    } else {
			        return false;
			    }
		},
		inputChange1(e){
			var num=e.detail.value;
			if(this.judgeNum(num)){
				
			}else{
				uni.showToast({
					title : '请输入正确的手机号码',
					icon : 'none',
				})
			}
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		inputChange2(e){
			var num=e.detail.value;
			if(this.judgeNum(num)){
				
			}else{
				uni.showToast({
					title : '请输入正确的验证码',
					icon : 'none',
				})
			}
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		loginClick(){	 //登录按钮
			this.logining=true;
			var that=this;
			const {phoneNumber, captchaCode} = this;		
			var phone=this.phoneNumber;
			var captcha=this.captchaCode;
			if(phone==null||phone==""){
				uni.showToast({
					title:"请输入手机号码",
					icon:"none"
				})
			}else{
				if(captcha==null||captcha==""){
					uni.showToast({
						title:"请输入验证码",
						icon:"none"
					})
				}else{
					uni.getStorage({
						key:'captchaCode',
						success(res) {
							if(captcha==res.data){
							var randomNum = ('000000' + Math.floor(Math.random() * 999999)).slice(-6);	
								uni.setStorage({
									key:'userInfo',
									data:{
										phoneNumber:phone,
										nickName:"用户"+randomNum
									}
								})
								uni.getStorage({
									key:'userInfo',
									success:function(user){
										that.login(user.data);
									}
								})
								uni.login({
									success() {
										uni.showToast({
											title:"登录成功"
										})
									}
								})
								 uni.switchTab({
									url:'/pages/index/index',
								}) 
							}else{
								uni.showToast({
									title:"验证码错误",
									icon:"none"
								})
							}
						}
					})
				}
			}
			
		},
		getCodeClick(e){	//获取验证码
			var self=this;
			const {phoneNumber, captchaCode} = this;		
			if(self.judgeNum(self.phoneNumber)){
				var timer=null,second=60; //倒计时的时间
				if(self.textCode == "获取验证码"){
				  //获取6位随机数
				  var randomNum = ('000000' + Math.floor(Math.random() * 999999)).slice(-6);
				  //短信接口
				  uni.setStorage({
						key:'captchaCode',
						data:randomNum
				  })
					uni.request({
					   url: 'http://111.231.109.113:8000/api/MyTest/SendMessage',
					   data:{
						  phoneNumber:self.phoneNumber,
						  text:'动态验证码：'+randomNum+',如果不是本人请忽略此短信。'
					   },
					   method:"GET",
					   success: function (res) {
							console.log(res);
							if(res.data){
								timer=setInterval(function(){
								second--;
								if(second<=0){	
									self.textCode = "获取验证码";
									clearInterval(timer);
									second=60;	
								}
								else{			
									self.disabled = true;
									self.textCode = second+"秒后重发";
								}},1000)
							}else{
								uni.showToast({
									title : '手机号码有误',
									icon : 'none',
								})
							}
					   }
					})	
				}
			}else{
				uni.showToast({
					title : '请输入正确的手机号码',
					icon : 'none',
				})
			}
		},
		returnClick(){		//返回个人中心
			uni.switchTab({
				url:'/pages/GRZX/user'
			})
		},
	}
}
